@import url(https://fonts.googleapis.com/css?family=Roboto);
@import url(https://fonts.googleapis.com/css?family=Roboto+Slab);
html,body{margin:0;padding:0;box-sizing:border-box;font-family:Roboto;font-size:18px}html *,html *::after,html *::before,body *,body *::after,body *::before{box-sizing:inherit}
.Appli .ajoutRessource{position:fixed;bottom:30px;right:30px;background-color:#333}.Appli .ajoutRessource:hover{background-color:#444}
.Entete{background-color:#000;color:#fff;display:flex;align-items:center;justify-content:space-between;height:60px;font-size:1.1em}.Entete>*{display:flex;padding:0 20px}.Entete .utilisateur{align-items:center}.Entete .utilisateur .avatar{margin-left:20px;width:35px;height:35px}
.ListeDossiers{display:flex;list-style:none;padding:10px 20px;justify-content:center;flex-wrap:wrap}
.Dossier{position:relative;width:300px;height:350px;margin:10px;color:#fff;cursor:pointer}.Dossier .deplacer{position:absolute;top:10px;left:10px;width:1.4em;height:1.4em;cursor:-webkit-grab;cursor:grab;background-color:rgba(0,0,0,.4);color:#ccc}.Dossier .deplacer:hover{background-color:rgba(0,0,0,.5)}.Dossier .info{padding:20px 15px}.Dossier .info h2{margin:0;padding:0;font-weight:normal;font-family:Roboto Slab}.Dossier .modifier{position:absolute;bottom:10px;right:10px;background-color:rgba(51,51,51,.1);color:#ccc}.Dossier .modifier:hover{background-color:rgba(51,51,51,.2)}
